        blog_description:`The term "Nada" is a Sanskrit word which means musical sound, "Nada"aw is a combination of "Nakar" means`,
        blog_content: 'The term "Nada" is a Sanskrit word which means musical sound, "Nada"aw is a combination of "Nakar" means life or breath and "Dakar" means fire or energy. When there is a combination of breath and energy "Nada" or musical sound is produced. In other words, when breath is energised it gives rise to musical sound or "Nada". Thus, sound is produced by motion of some kind in the air or any energetic action in the air causes sound.<br/><br/>In fact, sound is a sensation caused by certain oscillatory motions of the particles of a body. Some sounds are melodious and some sounds are harsh and unpleasant to hear. But in every case the source of sound is in a state of vibration. If you closely watch anything which emits sound, you will observe a sort of vibration in it and along with it the sound. If you just pluck the string of a "Sitar" or a "Tanpura" ( Musical Instruments) with your finger, it will emit sound. On close observation the string will appear to vibrate. If you again touch the vibrating string with your finger, the vibration will stop and along with it the sound will also cease. Thus, it is clear that vibration is the cause of sound.<br/><br/>These vibrations continue one after the other for some time and stop after a while. When such vibrations are uniform they are called "Regular Vibrations". But when such vibrations are flitting , alternately fast and slow, they are called "Irregular Vibrations". Then again these vibrations last up to a stated interval of time ad sometimes these vibrations suddenly stop. When these vibrations last for a stated interval of time , they are called "Non - Periodic vibrations" . Now the sound that is produced by regular and periodic vibrations is called unmusical sound or noise. In other words, a musical sound is produced by regular and continuous vibrations of a body but an unmusical sound or noise is produced by irregular and discontinuous vibrations of a body. Therefore, it can be said that "A musical note is the result of a regular and periodic vibrations of the air particles acting in the air, and therefore also of the body whence they proceed each particle passing through the same phrase at stated intervals of time. On the other hand, the motion to which noise is due is irregular and fitting, alternately fast and slow, and creating in the mind a bewildering and confusing effect of a more or less unpleasant character". "Encyclopedia Britanica", Vol. I ninth edition. In other words, musical sound is produced by regular and periodic vibrations. It produces continuous and pleasing sensation on the ear. While noise is the result of irregular and non-periodic vibrations. It is a confused mixture of many discordant sounds. It creates in the mind a bewildering and confusing effect of an unpleasant character.',
